/* eslint-disable no-unused-vars */
import React from 'react'
import { IconButton } from '@material-ui/core'
import { useNavigate } from 'react-router-dom'
import { Typography } from '@mui/material'
import Dialog from '@mui/material/Dialog'
import DialogTitle from '@mui/material/DialogTitle'
import DialogContent from '@mui/material/DialogContent'
import useStyles from './partners-form-success.styles'
import IconClose from '../../../assets/Icons/IconClose/IconClose'

import pic from '../../../assets/success.svg'

function PartnersFormSuccessPage() {
  const classes = useStyles()
  const navigate = useNavigate()

  const handleClose = () => {
    navigate('/partners-form')
  }

  return (
    <div>
      <Dialog open onClose={handleClose} disableScrollLock className={classes.root} hideBackdrop>
        <IconButton onClick={handleClose} className={classes.closePopup}>
          <IconClose />
        </IconButton>
        <DialogTitle className={classes.dialogTitle}>Форма успешно отправлена</DialogTitle>
        <DialogContent className={classes.dialogContent}>
          <Typography className={classes.formText} mb="40px">
            Спасибо за ответы на вопросы по вакансии! Мы проверим данные и опубликуем вакансию в сообществе Практикума
          </Typography>
          <img src={pic} alt="Success" style={{ display: 'block' }} />
        </DialogContent>
      </Dialog>
    </div>
  )
}

export default React.memo(PartnersFormSuccessPage)
